@import "libs/magnific-popup.css";

@font-face {
  font-family: 'Roboto';
  src: url('../fonts/Roboto-Regular.eot');
  src: url('../fonts/Roboto-Regular.eot?#iefix') format('embedded-opentype'), url('../fonts/Roboto-Regular.woff2') format('woff2'), url('../fonts/Roboto-Regular.woff') format('woff'), url('../fonts/Roboto-Regular.ttf') format('truetype'), url('../fonts/Roboto-Regular.svg#Roboto-Regular') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Merriweather';
  src: url('../fonts/Merriweather-Regular.eot');
  src: url('../fonts/Merriweather-Regular.eot?#iefix') format('embedded-opentype'), url('../fonts/Merriweather-Regular.woff2') format('woff2'), url('../fonts/Merriweather-Regular.woff') format('woff'), url('../fonts/Merriweather-Regular.ttf') format('truetype'), url('../fonts/Merriweather-Regular.svg#Merriweather-Regular') format('svg');
  font-weight: normal;
  font-style: normal;
}

.debug {
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 1200px;
  padding-left: 40px;
  padding-right: 40px;
  margin: 0 auto;
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.debug > div > div > div {
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
  width: calc(8.333333333333334% - 20px);
  height: inherit;
  outline: 1px solid #ff0;
  background: rgba(0, 0, 0, 0.5);
}

/* Defaults */

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  color: #1A1A1A;
  min-width: 300px;
}

button:focus {
  outline: none;
}

input:focus {
  outline: none;
}

a {
  text-decoration: none;
}

.wrapper {
  max-width: 1200px;
  padding-left: 40px;
  padding-right: 40px;
  margin: 0 auto;
}

header,
section,
footer {
  max-width: 1920px;
  margin: 0 auto;
}

section {
  padding: 40px 0;
}

/* Заголовок секции */

.section {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.section-title {
  font-family: 'Merriweather', sans-serif;
  font-size: 36px;
  color: #000;
  position: relative;
  padding-left: 30px;
}

.section-title::before {
  content: '';
  display: block;
  width: 19px;
  height: 30px;
  background: url(../img/about/title-icon.png) no-repeat;
  position: absolute;
  top: 3px;
  left: 0;
}

/* Блок с текстом */

.text {
  box-sizing: border-box;
  max-width: 1070px;
  padding-left: 30px;
  position: relative;
}

.text p {
  line-height: 29px;
  margin-bottom: 20px;
  font-weight: 300;
}

/* Кнопка */

.btn {
  font-family: 'Roboto', sans-serif;
  display: inline-block;
  font-size: 16px;
  color: #fff;
  background: #17594A;
  padding: 10px 50px;
  transform: skew(-45deg);
  border: 2px solid transparent;
}

.btn b {
  display: inline-block;
  transform: skew(45deg);
}

.btn:hover {
  color: #e6e6e6;
  background: #124539;
}

/* Хлебные крошки */

.breadcrumbs {
  padding: 0 30px;
  margin-bottom: 30px;
}

.breadcrumbs-link {
  font-size: 12px;
  color: #AFAFAF;
}

.breadcrumbs-link::after {
  content: '/';
  color: #CDCDCD;
  padding-left: 10px;
  box-sizing: border-box;
}

.breadcrumbs-link:last-child::after {
  content: '';
}

.breadcrumbs-link_active {
  color: #17594A;
}

.breadcrumbs-link:hover {
  text-decoration: underline;
}

/* ======  Header  ======= */

.header {
  background: url(../img/header/header-bg.png) center top no-repeat;
  background-attachment: fixed;
  padding: 75px 0px 50px;
  text-align: center;
}

.header-title {
  font-family: 'Merriweather', sans-serif;
  font-size: 44px;
  font-weight: bold;
}

.header-title a {
  color: #000;
}

.header-subtitle {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  color: #000;
  margin-bottom: 20px;
  max-width: 468px;
  margin: 20px auto;
}

/* ======  About  ======= */

.about {
  background: url(../img/about/about-bg_together.png) left center no-repeat;
  padding-top: 10px;
}

.about .section {
  justify-content: flex-start;
}

/* ======  Offers  ======= */

.offers {
  background: #F0F2F2 url(../img/offers/offers-bg.png) center top no-repeat;
}

.offers .offersWrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  justify-content: center;
}

.offers .offersBlock {
  background: #fff;
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
  width: calc(25% - 20px);
}

.offers .offersBlock-img {
  padding: 20px 0;
  text-align: center;
  margin-top: auto;
}

.offers .offersBlock-link {
  background: #34A78D;
  padding: 10px;
  min-height: 60px;
  display: flex;
  align-items: center;
  margin-top: auto;
  position: relative;
}

.offers .offersBlock-link a {
  font-size: 13px;
  line-height: 18px;
  color: #fff;
  max-width: 200px;
}

.offers .offersBlock-link a::after {
  content: "→";
  font-size: 26px;
  color: #fff;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  margin-right: 20px;
}

/* ======  Sertificates  ======= */

.sertificates {
  padding-bottom: 80px;
}

.sertificates .sertificatesWrap .tabs {
  margin-bottom: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.sertificates .sertificatesWrap .tabs .tab {
  font-size: 13px;
  border: 1px solid rgba(10, 65, 52, 0.5);
  box-sizing: border-box;
  border-radius: 2px;
  padding: 14px 10px 10px;
  cursor: pointer;
  flex-grow: 1;
  text-align: center;
  margin-right: 15px;
}

.sertificates .sertificatesWrap .tabs .tab:last-child {
  margin-right: 0;
}

.sertificates .sertificatesWrap .tabs .active {
  color: #fff;
  background: #0A4134;
}

.sertificates .sertificatesWrap .tab_item {
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.sertificates .sertificatesWrap .tab_item .sertificatesBlock {
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
  width: calc(25% - 20px);
  text-align: center;
}

.sertificates .sertificatesWrap .tab_item .sertificatesBlock:first-child {
  text-align: left;
}

.sertificates .sertificatesWrap .tab_item .sertificatesBlock:last-child {
  text-align: right;
}

/* Styles for MaginificPopup*/

/* overlay at start */

.mfp-fade.mfp-bg {
  opacity: 0;
  -webkit-transition: all 0.5s ease-out;
  -moz-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

/* overlay animate in */

.mfp-fade.mfp-bg.mfp-ready {
  opacity: 0.8;
}

/* overlay animate out */

.mfp-fade.mfp-bg.mfp-removing {
  opacity: 0;
}

/* content at start */

.mfp-fade.mfp-wrap .mfp-content {
  opacity: 0;
  transform: scale(0) rotate(180deg);
  -webkit-transition: all 0.6s ease-out;
  -moz-transition: all 0.6s ease-out;
  transition: all 0.6s ease-out;
}

/* content animate it */

.mfp-fade.mfp-wrap.mfp-ready .mfp-content {
  opacity: 1;
  transform: scale(1.5) rotate(0deg);
}

/* content animate out */

.mfp-fade.mfp-wrap.mfp-removing .mfp-content {
  opacity: 0;
}

/* ======  Provider  ======= */

.provider {
  background: #E2E2E2 url(../img/provider/provider-bg.png) center top no-repeat;
  background-size: cover;
  padding-bottom: 20px;
}

.provider .section {
  justify-content: flex-start;
}

/* ======  Сonsult  ======= */

.consult {
  background: #E2E2E2 url(../img/consult/consult-bg.png) left top no-repeat;
  text-align: center;
  padding: 80px 0;
}

.consult .section-title {
  color: #fff;
}

.consult .section-title::before {
  background: url(../img/about/title-icon_white.png) no-repeat;
}

.consult-subtitle {
  font-size: 26px;
  color: #fff;
  margin-bottom: 30px;
}

/* Modal */

.overlay {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3000;
  cursor: pointer;
}

.modal {
  display: none;
  width: 400px;
  background: #c5c5c5;
  border-radius: 5px;
  box-sizing: border-box;
  padding: 30px 25px;
  text-align: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3001;
}

.modal-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}

.modal-input {
  margin-bottom: 10px;
  border: 1px solid #DEDEEA;
  border-radius: 5px;
  background: #eee;
  box-sizing: border-box;
  padding: 10px 20px;
  color: #989898;
}

.modal-input::-webkit-input-placeholder {
  font-size: 18px;
  opacity: 0.8;
}

.modal-input::-moz-placeholder {
  font-size: 18px;
  opacity: 0.8;
}

.modal-btn {
  margin-top: 20px;
}

.modal-btn:hover {
  cursor: pointer;
}

.modal-close {
  position: absolute;
  top: 0;
  right: 0;
  margin: 15px;
}

.modal-close:hover {
  cursor: pointer;
}

.modal .required {
  font-size: 12px;
  font-weight: 300;
  margin-top: 10px;
  opacity: .8;
}

.up {
  display: none;
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 20px 50px;
  cursor: pointer;
  padding: 5px 15px;
  background: #fff;
  border: 2px solid #ccc;
  border-radius: 5px;
  color: #000;
  font-size: 14px;
}

.up:hover {
  background: #fff;
  color: #000;
}

/* libs */

/* Arrows */

.slick-prev,
.slick-next {
  border: 0;
  color: transparent;
  font-size: 0;
  width: 27px;
  height: 82px;
  background: url("../../img/gratitude/slider-arrow.png") no-repeat;
  position: absolute;
  top: 50%;
  z-index: 1;
  transform: translateY(-50%);
  opacity: .8;
}

.slick-prev:focus,
.slick-next:focus {
  outline: 0;
}

.slick-prev:hover,
.slick-next:hover {
  opacity: 1;
  cursor: pointer;
}

.slick-prev {
  left: -50px;
}

.slick-next {
  right: -50px;
  background-position: -27px 0;
}

/* Dots */

.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -20px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  margin: 0 10px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  padding: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 1px solid #00C4DF;
  box-sizing: border-box;
  background: #fff;
  color: transparent;
  font-size: 0;
}

.slick-dots li button:hover {
  cursor: pointer;
  opacity: 1;
}

.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
}

.slick-dots li.slick-active button {
  background-color: #00C4DF;
  opacity: 1;
  position: relative;
}

.slick-dots li.slick-active button::after {
  content: '';
  display: block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 1px solid #00C4DF;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

.slick-slide:focus {
  outline: 0;
}

.slick-slide img {
  display: inline-block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

