@import "project.less";
@import "smart-grid.less";


.debug{
    .debug(rgba(0, 0, 0, 0.5), 1px solid #ff0);
}


/* Defaults */
.reset();

body {
 .reg();
	font-size: 20px;
	color: @color_text-main;
	min-width: 300px;

	.sm-block({
				font-size: 16px;
		});

	.xs-block({
				font-size: 14px;
		});
}

button {
			&:focus {
						outline: none
			}
}

input, {
	&:focus {
		outline: none; 
	}
}

a {
	text-decoration: none;
}

.wrapper {
	.wrapper();
}

header,
section,
footer {
		max-width: 1920px;
		margin: 0 auto;
}

section {
			padding: 40px 0;

			.md-block({
						padding: 30px 0;
			});
}


/* Заголовок секции */ 
.section {
			display: flex;
			justify-content: center;
			margin-bottom: 30px;

			&-title {
						.reg-merri;
						font-size: 36px;
						color: @black;
						position: relative;
						padding-left: 30px;

						&::before {
								content: '';
								display: block;
								width: 19px;
								height: 30px;
								background: url(../img/about/title-icon.png) no-repeat;
								position: absolute;
								top: 3px;
								left: 0;

								.md-block({
											height: 0;
									});
						}

						.md-block({
									font-size: 26px;
									font-weight: bold;
							});

						.sm-block({
									font-size: 20px;
							});
			}
}


/* Блок с текстом */ 
.text {
		box-sizing: border-box;
		max-width: 1070px;
		padding-left: 30px;
		position: relative;

		p {
				line-height: 29px;
				margin-bottom: 20px;
				font-weight: 300;
		}
}


/* Кнопка */ 
.btn {
		.reg;
		display: inline-block;
		font-size: 16px;
		color: @white;
		background: @bg_btn;
		padding: 10px 50px;
		transform: skew(-45deg);
		border: 2px solid transparent;

		.md-block({
					display: block;
					max-width: 220px;
					padding: 7px 20px;
					margin: 0 auto;
					text-align: center;
			});

		b {
				display: inline-block;
				transform: skew(45deg);
		}

		&:hover {
					color: darken(@white, 10%);
					background: darken(@bg_btn, 5%);
		}
}


/* Хлебные крошки */ 
.breadcrumbs {
			padding: 0 30px;
			margin-bottom: 30px;

			&-link {
						font-size: 12px;
						color: #AFAFAF;

						&::after {
							content: '/';
							color: #CDCDCD;
							padding-left: 10px;
							box-sizing: border-box;
						}

						&:last-child {
									&::after {
												content: '';
									}
						}

						&_active {
							color: #17594A;
						}

						&:hover {
							text-decoration: underline;
						}
			}

	
}



/* ======  Header  ======= */
.header {
			background: url(../img/header/header-bg.png) center top no-repeat;
			background-attachment: fixed;
			padding: 75px 0px 50px;
			text-align: center;

			.sm-block({
						padding: 50px 0px;
				});

			&-title {
				.reg-merri;
				font-size: 44px;
				font-weight: bold;

						.xs-block({
									font-size: 30px;
							});

				a {
						color: @black;
				}
			}

			&-subtitle {
				.reg;
				font-size: 18px;
				color: @black;
				margin-bottom: 20px;
				max-width: 468px;
				margin: 20px auto;

				.xs-block({
							font-size: 14px;
					});
			}
}



/* ======  About  ======= */
.about {
			background: url(../img/about/about-bg_together.png) left center no-repeat;
			padding-top: 10px;

			.section {
						justify-content: flex-start;
			}

}


/* ======  Offers  ======= */
.offers {
		background: #F0F2F2 url(../img/offers/offers-bg.png) center top no-repeat;

		.offersWrap {
				.row-flex;
				justify-content: center;
		}

		.offersBlock {
				background: @white;
				margin-bottom: 40px;
				display: flex;
				flex-direction: column;

				.col;
				.size(3);

				.md-block({
							.size(4);
					});

				.sm-block({
							.size(6);
					});

				.xs-block({
							.size(11);
					});

				&-img {
						padding: 20px 0;
						text-align: center;
						margin-top: auto;
				}

				&-link {
							background: @offer_link;
							padding: 10px;
							min-height: 60px;
							display: flex;
							align-items: center;
							margin-top: auto;
							position: relative;
							
							a {
								font-size: 13px;
								line-height: 18px;
								color: @white;
								max-width: 200px;

								&::after {
										content: "→";
										font-size: 26px;
										color: @white;
										position: absolute;
										top: 50%;
										right: 0;
										transform: translateY(-50%);
										margin-right: 20px;
								}
							}
				}
		}

}




/* ======  Sertificates  ======= */
.sertificates {
			padding-bottom: 80px;

			.md-block({
						padding: 20px;
				});

			.sertificatesWrap  { 

					.tabs {
							margin-bottom: 30px;
							display: flex;
							flex-wrap: wrap;
							justify-content: space-between;

							.tab {
									font-size: 13px;
									border: 1px solid rgba(10, 65, 52, 0.5);
									box-sizing: border-box;
									border-radius: 2px;
									padding: 14px 10px 10px;
									cursor: pointer;	
									flex-grow: 1;
									text-align: center;
									margin-right: 15px;

									.lg-block({
													margin-bottom: 10px;
													max-width: 300px;
										});

									.md-block({
													padding: 12px 10px 8px;
										});

									&:last-child {
											margin-right: 0;
									}
							}

						 .active  {
										color: @white; 
										background: @bg_tabs;
							}
					}


					.tab_item {
									margin-bottom: 20px;
									.row-flex;

									.sertificatesBlock {
												.col;
												.size(3);
												text-align: center;

												&:first-child {
														text-align: left;
												}

												&:last-child {
													text-align: right;
												}

												.md-block({
															.size(4);
															margin-bottom: 20px;

															&:first-child {
																	text-align: left;
															}

															&:last-child {
																text-align: left;
															}
													});

												.sm-block({
															.size(6);

															&:first-child {
																	text-align: center;
															}

															&:last-child {
																text-align: center;
															}
													});

												.xs-block({
															.size(12);
													});

									}
									
					}
							
			}


}


/* Styles for MaginificPopup*/ 

/* overlay at start */
.mfp-fade.mfp-bg {
  opacity: 0;

  -webkit-transition: all 0.5s ease-out;
  -moz-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}
/* overlay animate in */
.mfp-fade.mfp-bg.mfp-ready {
  opacity: 0.8;
}
/* overlay animate out */
.mfp-fade.mfp-bg.mfp-removing {
  opacity: 0;
}

/* content at start */
.mfp-fade.mfp-wrap .mfp-content {
  opacity: 0;
  transform: scale(0) rotate(180deg);

  -webkit-transition: all 0.6s ease-out;
  -moz-transition: all 0.6s ease-out;
  transition: all 0.6s ease-out;
}
/* content animate it */
.mfp-fade.mfp-wrap.mfp-ready .mfp-content {
  opacity: 1;
  transform: scale(1.5) rotate(0deg);

  .xs-block({
  			transform: scale(1.1) rotate(0deg);
  	});
}
/* content animate out */
.mfp-fade.mfp-wrap.mfp-removing .mfp-content {
  opacity: 0;
}







/* ======  Provider  ======= */
.provider {
			background: #E2E2E2 url(../img/provider/provider-bg.png) center top no-repeat;
			background-size: cover;
			padding-bottom: 20px;
		
			.section {
						justify-content: flex-start;
			}

}




/* ======  Сonsult  ======= */

.consult {
			background: #E2E2E2 url(../img/consult/consult-bg.png) left top no-repeat;
			text-align: center;
			padding: 80px 0;

			.md-block({
						padding: 50px 0;
			});

			.section {
						&-title {
									color: @white;

									&::before {
											background: url(../img/about/title-icon_white.png) no-repeat;
									}
						}
			}

			&-subtitle {
						font-size: 26px;
						color: @white;
						margin-bottom: 30px;

						.sm-block({
									font-size: 18px;
						});
			}

}





/* Modal */ 

.overlay {
	width: 100%;
	height: 100%;
	background: rgba(0,0,0, 0.7);

	position: fixed;
	top: 0;
	left: 0;
	z-index: 3000;
	cursor: pointer;
}

.modal {
			display: none;
			width: 400px;
			
			background: #c5c5c5;
			border-radius: 5px;
			box-sizing: border-box;
			padding: 30px 25px;
			text-align: center;

			position: fixed;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			z-index: 3001;


			.xs-block({
						width: 280px;
				});

			&-title {
						font-size: 20px;
						font-weight: bold;
						margin-bottom: 20px;
			}

			&-input {
						margin-bottom: 10px;
						border: 1px solid #DEDEEA;
						border-radius: 5px;
						background: #eee;
						box-sizing: border-box;
						padding: 10px 20px;
						color: #989898;

						&::-webkit-input-placeholder {
								font-size: 18px;
								opacity: 0.8;
							}

						&::-moz-placeholder {
								font-size: 18px;
								opacity: 0.8;
							}
			}

			&-btn {
				margin-top: 20px;

				&:hover {
					cursor: pointer;
				}

				.sm-block({
						margin-top: 20px;
					});
			}

			&-close {
						position: absolute;
						top: 0;
						right: 0;
						margin: 15px;

						&:hover {
							cursor: pointer;
						}
			}

			.required {
					font-size: 12px;
					font-weight: 300;
					margin-top: 10px;
					opacity: .8;
			}
}


.up {
		display: none;
		position: fixed;
		bottom: 0;
		right: 0;
		margin: 20px 50px;
		cursor: pointer;
		padding: 5px 15px;
		background: @white;
		border: 2px solid #ccc;
		border-radius: 5px;
		color: @black;
		font-size: 14px;

		&:hover {
			background: @white;
			color: @black;
		}
}



/* libs */
@import "libs/slick.less";
@import "libs/magnific-popup.css";




